import { _decorator, Component, Node } from 'cc';
import { ListViewCtrl2 } from '../../../game/components/listViewCtrl2';
import { modelMgr, observer } from '../../../game/App';
import { EventType } from '../../../EventType';
import { taskcell } from '../../../game/config/DataStruct';
import RedManager from '../../../manager/RedManager';
const { ccclass, property } = _decorator;

@ccclass('RenwuComponent')
export class RenwuComponent extends Component {
    @property(Node)
    pre_listView: Node = null;

    start() {

        observer.on(EventType.SOCKET_TASKLIST, this.updateRenwuList, this);
    }


    protected onEnable(): void {

        //排序逻辑
        let arr = modelMgr.configModel.commonConfigJson.TaskCfg;
        this.showRenwuList(arr);
    }

    private updateRenwuList() {
        // let da: taskcell[] = modelMgr.mainModel.tasklist.data;
        // let arr = modelMgr.configModel.commonConfigJson.TaskCfg;
        // this.showRenwuList(arr);
    }

    
    /**
* 
*/
    public showRenwuList(arr) {

        if (this.pre_listView) {
            let ctrl = this.pre_listView.getChildByName("listctrl");
            let spawnCount = arr.length;
            ctrl.getComponent(ListViewCtrl2).initData(spawnCount, arr);
        }
    }

    update(deltaTime: number) {

    }
}


